  <menuF4 v-if="windowF4" @set-action="setAction('menuF4', $event)" :playerName="windowF4.playerName" :gradeName="windowF4.gradeName" />
</template>

<script>
import menuF4 from "./components/menuF4.vue";

export default {
  name: "App",
  components: {
    menuF4,
  },
  methods:{
    setWindowF4(playerName = false, gradeName) {
        if(playerName) {
            this.windowF4 = {
                playerName,
                gradeName
            };
        } else {
            this.windowF4 = false;
        }
    },
    setAction(category, action) {
      if(!window.mcefQuery) {
          console.log("Dev :" + category + ": :" + action);
      } else {
          window.mcefQuery({
              request: category + "::" + action,
              persistent: false,
              onSuccess: (msg) => {
                  console.log("[DarkRP] " + msg);
              },
              onFailure: (err, msg) => {
                  console.log("[DarkRP] " + msg);
              }
          });
      }
    }
  },
  data: function() {
      return {
         windowF4 : false,
      }
   },
  mounted() {
    window.vue=this;
   /* let recaptchaScript = document.createElement("script");
    recaptchaScript.setAttribute("src", "@/jquery.js");
    document.head.appendChild(recaptchaScript);*/
      if(!window.mcefQuery) {
          console.log("Dev nope");
      } else {
          window.mcefQuery({
              request: "username",
              persistent: false,
              onSuccess: (msg) => {
                  console.log("[DarkRP] " + msg);
              },
              onFailure: (err, msg) => {
                  console.log("[DarkRP] " + msg);
              }
          });
      }
  },
};
</script>


